.main-reviews {
	@include sm {
		padding-bottom: 10px; }
	&__row {
		margin: 0 -10px;
		flex-wrap: wrap;
		display: flex; }
	&__col {
		margin-bottom: 20px;
		padding: 0 10px;
		@include fs(33.3%);
		@include md {
			@include fs(50%); }
		@include sm {
			@include fs(100%); } }
	&__footer {
		margin-top: 74px;
		text-align: center;
		@include sm {
			margin-top: 20px; }
		.btn {
			@include sm {
				width: 100%; } } } }

.review {
	padding: 20px;
	height: 100%;
	background: #F7F7F8;
	border-radius: 12px;
	flex-direction: column;
	justify-content: space-between;
	display: flex;
	@include sm {
		margin: 0 -15px; }
	&_lg {
		flex-direction: row;
		@include sm {
			flex-direction: column; } }
	&_mb {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__hero {
		margin-bottom: 20px;
		align-items: center;
		display: flex;
		&_sb {
			justify-content: space-between; }
		.review_lg & {
			margin-bottom: 20px; } }
	&__avatar {
		margin-right: 15px;
		flex-shrink: 0;
		width: 90px;
		height: 90px;
		border-radius: 50%;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center; } }
	&__name {
		line-height: 1.22;
		font-size: 18px;
		@include fr; }
	&__logo {
		margin-right: 25px;
		flex-shrink: 0;
		width: 90px;
		height: 90px;
		border-radius: 8px;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center; } }
	&__out {
		flex-grow: 1; }
	&__text {
		color: #656674; }
	&__images {
		margin-top: 20px;
		font-size: 0;
		white-space: nowrap;
		overflow: hidden;
		max-width: 360px;
		@include sm {
			max-width: 334px; } }
	&__img {
		position: relative;
		width: 70px;
		height: 70px;
		display: inline-block;
		vertical-align: top;
		@include sm {
			width: 65px;
			height: 65px; }
		&:not(:first-child) {
			margin-left: 2px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			display: block; }
		.review-image-lenght {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			color: #fff;
			font-weight: 700;
			text-align: center;
			line-height: 70px;
			font-size: 18px;
			background: rgba(0,0,0,0.50);
			span {
				font-weight: 400; } } }
	&__footer {
		margin-top: 20px;
		color: #656674;
		.review_lg & {
			font-weight: 600; } } }

.reviews-footer {
	margin-top: 40px;
	text-align: center;
	.btn {
		@include sm {
			width: 100%; }
		&:not(:last-child) {
			margin-right: 15px;
			@include sm {
				margin: 0 0 20px; } } } }
