// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
// +generate_grid($columns: 3, $sizeXs: false, $sizeSm: false, $sizeMd: false, $sizeLg: true, $padding: 30px)

@include generate_grid();

.out {
	position: relative;
	overflow: hidden; }

.inner {
	padding: 0 15px;
	margin: 0 auto;
	width: 100%;
	max-width: 1270px; }

.wrap {
	padding: 60px 0 80px;
	display: flex;
	@include md {
		padding: 50px 0 40px;
		display: block; }
	&_pt0 {
		padding-top: 0; }
	&_mdpb0 {
		@include md {
			padding-bottom: 0; } } }
.main-col {
	flex-grow: 1;
	overflow: hidden;
	@include md {
		overflow: visible; } }
.sidebar {
	margin-left: 40px;
	flex-shrink: 0;
	width: 350px;
	@include md {
		margin: 40px 0 0;
		width: auto; }
	.title-md,
	.item {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	.itemsm {
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__field {
		@include md {
			margin-bottom: 60px; }
		&:not(:first-child) {
			margin-top: 40px;
			@include md {
				margin-top: 60px; } }
		&:not(:last-child) {
			margin-bottom: 40px;
			@include md {
				margin-bottom: 60px; } } } }
