.header {
	&__inner {
		align-items: center;
		justify-content: space-between;
		display: flex; }
	&__top {
		padding: 25px 0;
		@include lg {
			padding: 20px 0; } }
	&__logo {
		margin-right: 59px;
		@include lg {
			margin-right: 15px; } }
	&__address {
		margin-right: auto;
		font-size: 14px;
		.header__nav & {
			@include lg {
				margin-top: 30px; } }
		span {
			margin-right: 10px;
			font-size: 12px;
			color: #45464D;
			.header__nav & {
				@include lg {
					color: rgba(#fff,.5); } } } }
	&__soc {
		display: flex;
		.header__nav & {
			@include lg {
				margin-top: 40px;
				display: flex;
				justify-content: space-between;
				align-items: flex-start; }
			@include xs {
				flex-direction: column; } }
		.btn {
			&:not(:last-child) {
				margin-right: 15px;
				@include xs {
					margin: 0 0 15px; } } } }
	&__bottom {
		position: relative;
		padding: 15px 0;
		color: $white;
		background-color: #181A21;
		@include lg {
			padding: 10px 0; } }
	&__nav {
		padding: 0 10px;
		@include lg {
			position: absolute;
			top: 100%;
			left: 0;
			padding: 20px 15px 25px;
			z-index: 10;
			@include fadeout(.3s);
			width: 100vw;
			background-color: #181A21;
			box-shadow: 0 12px 16px 0 rgba(#000, .2);
			.header.is-open & {
				@include fadein; } }
		.btn {
			@include sm {
				padding: 11px 15px; } } }
	&__phone {
		font-weight: 600;
		font-size: 21px;
		color: #FFFFFF;
		letter-spacing: 1px;
		text-align: right;
		transition: opacity .3s;
		@include hover {
			opacity: .6; }
		&_lgicon {
			@include lg {
				font-size: 0; } }
		.header__nav & {
			@include lg {
				margin-top: 56px;
				font-size: 30px;
				line-height: 1.2;
				text-align: left; }
			@include xs {
				font-size: 26px; } } } }

.btn-menu {
	position: relative;
	flex-shrink: 0;
	width: 40px;
	height: 40px;
	vertical-align: top;
	display: inline-block;
	background: #20232F;
	border-radius: 5px;
	&__wrap {
		position: absolute;
		left: 11px;
		top: 13px;
		width: 18px;
		height: 14px;
		transition: transform .3s;
		.header.is-open & {
			transform: translateX(3px); } }
	&__line {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 2px;
		background-color: #fff;
		display: block;
		border-radius: 2px;
		transition: transform .3s, opacity .3s;
		&:first-child {
			top: 0;
			transform-origin: top left;
			.header.is-open & {
				transform: rotate(45deg) translate(0, 0); } }
		&:nth-child(2) {
			top: 6px;
			transform-origin: left;
			.header.is-open & {
				opacity: 0;
				transform: scaleX(0); } }
		&:last-child {
			top: auto;
			bottom: 0;
			transform-origin: bottom left;
			.header.is-open & {
				transform: rotate(-45deg) translate(0, 0); } } } }


.logo {
	align-items: center;
	display: flex;
	img {
		margin-right: 40px;
		flex-shrink: 0;
		width: 52px;
		height: auto;
		@include lg {
			margin-right: 11px;
			width: 35px; } }
	&__wrap {
		flex-grow: 1;
		font-weight: 500; }
	&__top {
		font-size: 25px;
		line-height: 1.25;
		color: #181A21;
		display: block;
		@include lg {
			margin-bottom: 4px;
			font-size: 21px;
			line-height: 1.2; } }
	&__bottom {
		font-size: 14px;
		color: #858696;
		line-height: 1.285;
		display: block;
		@include lg {
			font-size: 10px;
			line-height: 1.3; } } }

.servicesmenu {
	z-index: 3;
	color: #181A21;
	&__trigger {
		padding: 15px 35px;
		min-width: 190px;
		@include fmt;
		font-weight: 500;
		font-size: 16px;
		text-align: left;
		color: #fff;
		background-color: #20232F;
		border-radius: 6px;
		transition: background-color .3s;
		@include hover {
			background-color: lighten(#20232F,20%); }
		.servicesmenu.is-open & {
			background-color: lighten(#20232F,20%); }
		.icon {
			margin: 1px 20px 0 0;
			line-height: 1;
			vertical-align: top; } }
	&__menu {
		position: absolute;
		top: 100%;
		left: 0;
		width: 100vw;
		@include fadeout(.3s);
		.servicesmenu.is-open & {
			@include fadein; } }
	&__wrap {
		padding: 55px 35px 116px;
		background: #FFFFFF;
		display: flex;
		box-shadow: 0 22px 66px -13px #E0E3E2;
		border-radius: 0 0 6px 6px;
		@include sm {
			padding: 30px 15px;
			flex-direction: column; }
		.no-touch & {
			margin-left: -15px;
			@include sm {
				margin: 0 -15px; } } }
	&__col {
		@include fs(calc(50% - 90px));
		padding-right: 30px;
		flex-grow: 1;
		overflow: hidden;
		@include sm {
			padding: 0;
			@include fs(100%); }
		&:not(:last-child) {
			margin-bottom: 40px; } }
	&__title {
		margin-bottom: 25px;
		font-weight: 500;
		@include fr;
		font-size: 25px;
		line-height: 1.3;
		letter-spacing: -.02em;
		color: #181A21; }
	&__list {
		font-size: 16px;
		line-height: 1.18;
		li {
			display: block;
			&:not(:last-child) {
				margin-bottom: 25px; } }
		a {
			color: currentColor;
			transition: color .3s, border-color .3s;
			border-bottom: 1px solid transparent;
			@include hover {
				color: #01AFF0;
				border-color: #01AFF0; } } }
	&__btn-col {
		width: 180px;
		flex-shrink: 0;
		align-self: flex-end;
		@include sm {
			width: 100%; } } }


.nav {
	font-weight: 500;
	font-size: 16px;
	@include lg {
		font-size: 18px;
		line-height: 1.2; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		.header__nav & {
			@include lg {
				flex-direction: column; } }
		li {
			&:not(:last-child) {
				margin-right: 35px;
				@include lg {
					margin: 0 0 25px; } } }
		a {
			color: currentColor;
			transition: opacity .3s;
			@include hover {
				opacity: .6; }
			&.is-active {
				font-weight: 500; } } } }
.soc {
	flex-wrap: wrap;
	display: flex;
	&__item {
		&:not(:last-child) {
			margin-right: 15px;
			@include lg {
				margin-right: 10px; } }
		a {
			width: 50px;
			height: 50px;
			line-height: 52px;
			text-align: center;
			border-radius: 10px;
			transition: background-color .3s;
			background-color: rgba(#1C1E25, .2);
			display: block;
			@include lg {
				height: 40px;
				width: 40px;
				line-height: 39px;
				border-radius: 8px; }
			@include hover {
				background-color: rgba(#1C1E25, .15); }
			.soc_white & {
				background-color: rgba(#fff, 0.1);
				border-radius: 6px;
				@include hover {
					background-color: rgba(#fff, .15); } } }
		img {
			max-width: 80%;
			max-height: 80%;
			vertical-align: middle;
			display: inline-block;
			@include lg {
				max-width: 50%;
				max-height: 50%; } } }
	.fab {
		color: #1C1E25;
		font-size: 18px; }
	&_white {
		.fab {
			color: #fff; } } }

