body {
	@include fmt;
	min-width: 375px;
	color: #181A21;
	@include f(16px,500,1.875);
	@include font-smoothing;
	// +fadeout(.2s)
	background: $white;
	// &.is-loaded
	// 	+fadein
	@include sm {
		font-size: 14px;
		line-height: 1.7; } }

.title {
	@include fr;
	@include f(44px,400,1.2);
	letter-spacing: -0.02em;
	@include sm {
		font-size: 30px; } }

.title-md {
	@include fr;
	@include f(30px,400,1.2);
	letter-spacing: -0.01em; }

.section {
	padding: 40px 0;
	@include sm {
		padding: 30px 0; }
	&_first {
		padding-top: 60px;
		@include sm {
			padding-top: 40px; } }
	&_last {
		padding-bottom: 80px;
		@include sm {
			padding-bottom: 60px; } }
	&_pt0 {
		padding-top: 0;
		@include sm {
			padding-top: 0; } }
	&_p0 {
		padding: 0;
		@include sm {
			padding: 0; } }
	&_mdpb0 {
		@include md {
			padding-bottom: 0; }
		@include sm {
			padding-bottom: 0; } }
	&_simpleslider {
		padding: 80px 0 20px;
		@include sm {
			padding: 40px 0 10px; } }
	&__title {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 40px; }
		.section_first & {
			@include sm {
				margin-bottom: 20px; } } }
	&__head {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 20px; }
		.section__title {
			margin-bottom: 15px; } }
	&__col {
		&_md-mb20 {
			&:not(:last-child) {
				@include md {
					margin-bottom: 20px; } } } } }

.text-left {
	text-align: left !important; }

.topic {
	padding: 60px 0 50px;
	@include sm {
		padding: 50px 0 40px; }
	&__link {
		position: relative;
		margin-left: 33px;
		@include fr;
		@include f(44px, 400, 1.2);
		color: #181A21;
		transition: color .3s;
		letter-spacing: -0.002em;
		@include sm {
			font-size: 30px; }
		@include hover {
			color: #01aff0;
			&:before {
				@include arr(9,14,#01aff0,l); } }
		&:before {
			position: absolute;
			content: '';
			top: 19px;
			left: -33px;
			@include arr(9,14,#181A21,l);
			transition: border-color .3s;
			@include sm {
				top: 11px; } } } }

.date {
	color: #656674; }

article {
	h1,h2,h3,h4,h5,h6 {
		@include fr; }
	h1,h2 {
		&:not(:first-child) {
			margin-top: 80px;
			@include sm {
				margin-top: 40px; } }
		&:not(:last-child) {
			margin-bottom: 30px;
			@include sm {
				margin-bottom: 20px;
				line-height: 1.16; } } }
	h3, h4,h5,h6 {
		&:not(:first-child) {
			margin-top: 30px;
			@include sm {
				margin-top: 20px; } }
		&:not(:last-child) {
			margin-bottom: 20px;
			@include sm {
				margin-bottom: 10px; } } }
	h1 {
		@include f(54px,400,1.2); }
	h2 {
		@include f(44px,400,1.2);
		@include sm {
			font-size: 25px; } }
	h3 {
		@include f(30px,400,1.3);
		@include sm {
			font-size: 21px; } }
	h4 {
		@include f(24px,500,1.3);
		@include sm {
			font-size: 18px; } }
	h5 {
		@include f(22px,600,1.3);
		@include sm {
			font-size: 16px; } }
	h6 {
		@include f(18px,700,1.3);
		@include sm {
			font-size: 14px; } }
	p {
		&:not(:last-child) {
			margin-bottom: 15px; } }
	blockquote {
		padding: 30px 40px 30px;
		background: #FEEBEF;
		border-radius: 6px;
		@include sm {
			margin-left: -15px;
			margin-right: -15px;
			padding: 30px 15px; }
		&:not(:first-child) {
			margin-top: 30px; }
		&:not(:last-child) {
			margin-bottom: 30px; } }
	ul {
		line-height: 1.875;
		&:not(:first-child) {
			margin-top: 30px; }
		&:not(:last-child) {
			margin-bottom: 20px; }
		li {
			position: relative;
			padding-left: 22px;
			&:not(:last-child) {
				margin-bottom: 4px; }
			&:before {
				position: absolute;
				content: '';
				top: 12px;
				left: 0;
				width: 3px;
				height: 3px;
				border-radius: 50%;
				background-color: #181A21; } } }
	img,
	figure {
		max-width: 100%;
		height: auto;
		display: block;
		&:not(:first-child) {
			margin-top: 35px; }
		&:not(:last-child) {
			margin-bottom: 40px; }
		& + h1:not(:first-child),
		& + h2:not(:first-child),
		& + h3:not(:first-child) {
			margin-top: 30px; } }
	a {
		color: #01AFF0;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: #01AFF0; } }
	.date {
		margin-bottom: 30px; }
	.simpleslider {
		&:not(:first-child) {
			margin-top: 40px; }
		&:not(:last-child) {
			margin-bottom: 20px; } } }
.lg-show {
	display: none !important;
	@include lg {
		display: block !important; } }
.lg-hide {
	@include lg {
		display: none !important; } }
.md-show {
	display: none !important;
	@include md {
		display: block !important; } }
.md-hide {
	@include md {
		display: none !important; } }

.sm-show {
	display: none !important;
	@include sm {
		display: block !important; } }
.sm-hide {
	@include sm {
		display: none !important; } }


.pagination {
	margin-top: 20px;
	text-align: center;
	a {
		color: currentColor;
		transition: color .3s;
		@include hover {
			color: #01AFF0; } }
	.page-numbers {
		margin: 0 5px; }
	.current {
		color: #01AFF0; }
	.next {
		margin: 0 0 0 40px;
		@include sm {
			display: none; } }
	.prev {
		margin: 0 40px 0 0;
		@include sm {
			display: none; } } }
