	@include sm {
		margin: 0 -15px;
		padding: 25px 15px 64px; }
	&__list {
		position: relative;
		font-size: 0;
		overflow: hidden;
		display: flex;
		&.slick-initialized {
			position: static;
			overflow: visible;
			display: block; } }
	&__title {
		margin-bottom: 40px;
		@include f(30px,500,1.2);
		@include fr;
		letter-spacing: -0.01em;
		text-align: left;
		@include sm {
			margin-bottom: 15px;
			font-size: 18px; } }
	.slick-arrow {
		position: absolute;
		bottom: 30px;
		right: 62px;
		width: 40px;
		height: 40px;
		border-radius: 8px;
		font-size: 0;
		background-color: #fff;
		border-radius: 6px;
		transition: transform .3s, opacity .3s;
		@include sm {
			width: 24px;
			height: 24px;
			right: 15px;
			bottom: 21px;
			border-radius: 4px; }
		@include hover {
			opacity: .7; }
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			transform: translate(-38%,-50%);
			@include arr(9,14,#01AFF0,r);
			@include sm {
				@include arr(6,9,#01AFF0,r); } } }
	.slick-prev {
		right: 112px;
		@include sm {
			right: 45px; }
		&:before {
			transform: translate(-62%,-50%) rotate(180deg); } }
	.slick-dots {
		position: absolute;
		left: 62px;
		bottom: 42px;
		flex-wrap: wrap;
		display: flex;
		@include sm {
			left: 15px;
			bottom: 28px; }
		li {
			&:not(:last-child) {
				margin-right: 10px;
				@include sm {
					margin-right: 6px; } }
			&.slick-active button {
				transform: scale(1);
				opacity: 1; } }
		button {
			font-size: 0;
			width: 8px;
			height: 8px;
			opacity: .8;
			transform: scale(0.5);
			transition: transform .3s, opacity .3s;
			border-radius: 50%;
			background-color: #fff; } }
	&_green {
