		.slick-arrow:before {
			@include arr(9,14,#51CEB6,r); } } }
.services-item {
	padding: 21px 15px;
	min-width: 100%;
	color: #fff;
	font-size: 18px;
	line-height: 1.51;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	background-color: #008CC0;
	border-radius: 6px;
	transition: background-color .3s;
	display: block;
	@include sm {
		padding: 11px 9px;
		font-size: 14px;
		line-height: 1.3; }
	@include hover {
		background-color: darken(#008CC0, 10%); }
	&_green {
		background-color: #20CAAE;
		@include hover {
			background-color: darken(#20CAAE, 10%); } }
	&:not(:last-child) {
		margin-bottom: 10px; } }
