.doctor {
	align-items: center;
	display: flex;
	@include md {
		flex-direction: column; }
	&:not(:last-child) {
		margin-bottom: 50px; }
	&__img {
		@include fs(36.29%);
		background: #EDEFF3;
		border-radius: 8px;
		overflow: hidden;
		@include md {
			margin-bottom: 15px; }
		@include sm {
			@include fs(100%); }
		img {
			width: 100%;
			height: 90%;
			object-fit: cover;
			object-position: center;
			display: block; } }
	&__img-wrap {
		background: #EDEFF3;
		border-radius: 8px;
		overflow: hidden;
		img {
			width: 100%;
			height: 10;
			align-self: flex-start;
			object-fit: cover;
			object-position: center;
			display: block; } }
	&__wrap {
		padding-left: 50px;
		@include md {
			padding-left: 0; } }
	&__hero {
		display: flex;
		@include md {
			flex-direction: column; } }
	&__hero-left {
		@include fs(65%);
		@include md {
			margin-top: 20px;
			@include fs(100%); } }
	&__hero-right {
		text-align: right;
		@include fs(35%);
		@include md {
			@include fs(100%);
			order: -1;
			text-align: left;
			justify-content: space-between;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start; } }
	&__name {
		@include fr;
		@include f(30px,500,1.2);
		display: inline-block;
		@include md {
			display: flex;
			justify-content: space-between; }
		@include sm {
			font-size: 21px;
			line-height: 1.2; } }
	&__specializations {
		margin-right: -15px; }
	&__experience {
		@include fmt;
		margin-left: 20px;
		font-size: 16px;
		@include f(16px,600,1.875);
		display: inline-block;
		@include md {
			align-self: flex-end; }
		&_ml0 {
			margin-left: 0; } }
	&__rating {
		margin: 7px 0 7px 0;
		line-height: 1;
		@include md {
			order: 2;
			margin: 0 0 0 15px; }
		.doctor__out & {
			margin: 0 0 0 10px; } }
	&__assessment {
		font-weight: 600;
		line-height: 1.625;
		@include md {
			order: 1;
			display: block; } }
	&__quantity {
		@include md {
			order: 3;
			@include fs(100%); } }
	&__row {
		margin-top: 20px;
		.doctor__right & {
			margin-top: 30px; } }
	&__col {
		&:not(:last-child) {
			@include sm {
				margin-bottom: 20px; } }
		&_mb {
			&:not(:last-child) {
				margin-bottom: 30px; } } }
	&__label {
		margin-bottom: 4px;
		font-size: 14px;
		color: #858696;
		line-height: 2.1; }
	&__footer {
		margin-top: 28px;
		@include sm {
			margin-top: 13px; }
		.btn {
			@include sm {
				width: 100%; } } }
	&__out {
		margin-top: 20px;
		justify-content: space-between;
		align-items: center;
		display: flex; }
	&__left {
		@include fs(53%);
		align-self: flex-start;
		@include lg {
			@include fs(30%); }
		@include md {
			margin: 0 auto;
			@include fs(100%);
			max-width: 400px; } }
	&__right {
		margin-top: 30px;
		padding-left: 40px;
		@include fs(47%);
		align-self: flex-start;
		@include lg {
			@include fs(70%); }
		@include md {
			padding: 0;
			@include fs(100%); } } }

.specialization {
	margin: 15px 15px 0 0;
	padding: 13px 19px;
	background-color: rgba(#01AFF0,.1);
	transition: background-color .3s;
	border-radius: 6px;
	font-size: 16px;
	line-height: 1.1875;
	font-weight: 600;
	color: #01AFF0;
	display: inline-block;
	@include sm {
		letter-spacing: -0.035em; } }
a.specialization {
	@include hover {
		background-color: rgba(#01AFF0,.2); } }
