.contacts {
	padding: 60px 0;
	&__inner {
		display: flex;
		@include md {
			flex-direction: column; } }
	&__left {
		@include fs(32.74%);
		@include md {
			@include fs(100%); } }
	&__title {
		margin-bottom: 40px; }
	&__field {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	&__label {
		margin-bottom: 10px;
		font-size: 14px;
		color: #858696; }
	&__item {
		font-weight: 600;
		line-height: 1.22;
		&:not(:last-child) {
			margin-bottom: 15px; } }
	&__map {
		@include fs(67.26%);
		@include md {
			margin: 40px -15px 0;
			@include fs(calc(100% + 30px)); }
		img {
			width: 100%;
			display: block; } } }
